import {useEffect} from 'react';
import Carousel from 'react-bootstrap/Carousel';
import {useStateContext} from '../contexts/ContextProvider';
import {Layout} from '../components';
import noImage from '../data/no-image.png';

const Home = () => {
    const {isLoggedIn, getImages, imageList, setImageUpload, uploadImage} = useStateContext();
    const imageListRef = `images/home/`;

    useEffect(() => {
        getImages(imageListRef);
    }, []);

    return (
        <Layout>
            {isLoggedIn && <div className="d-flex justify-content-end" id="gallery-actions">
                <input
                    multiple
                    type="file"
                    onChange={(event) => {
                    setImageUpload(event.target.files)
                }}/>
                <button type="button" className="btn btn-primary mx-1" onClick={uploadImage}>Dodaj Sliku</button>
                <button type="button" className="btn btn-danger mx-1 d-none">Obrii Sliku</button>
            </div>}
            <div className="container">
                <div className="row justify-content-center mt-5">
                    <div className="col-lg-7">
                        {imageList.length
                            ? <Carousel className="shadow-lg" fade>
                                    {imageList.map((item, index) => (
                                        <Carousel.Item key={index} interval={2000}>
                                            <img src={item} className="d-block w-100" alt="..." height="500"></img>
                                        </Carousel.Item>
                                    ))}
                                </Carousel>
                            : <img src={noImage} className="d-block w-100" alt="..." height="500"/>
                        }
                    </div>
                    <div className="col-lg-8 mt-4">
                        <p>"Kutije Letus" je porodina firma sa dugogodinjim iskustvom nastala iz elje
                            da runo radjenim elemetima da svoj peat.</p>
                        <p>Bavimo se izradom i prodajom svojih dokarativnih kutija i drugih srodnih
                            artikala za cveare i poklon opove, firme i pojedince koji se bave dokoracijom
                            objekata.</p>
                        <p>Uz potovanje naih saradnika i njihovih elja i potreba, trudimo se da
                            kvalitetom naih proizvoda i potovanjem dogovorenih rokova, na obostrano
                            zadovoljstvo donesemo radost, jer "Letus" znai radost.</p>
                        <p>Zavirite u na svet...</p>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Home
